import React from "react"
import Paper from "@material-ui/core/Paper"
import { makeStyles } from "@material-ui/core/styles"
import "../site.css"

const educationStyles = makeStyles({
  root: {
    display: `flex`,
    flexDirection: `column`,
    marginTop: `10px`,
    maxWidth: `700px`,
    padding: `10px`,
  },
  title: {
    color: `#121212`,
    fontSize: `1.6em`,
    fontWeight: 800,
    marginBottom: `10px`,
  },
  school: {
    fontSize: `17px`,
    lineHeight: `1.3`,
    color: `#121212`,
    fontWeight: `600`,
  },
  details: {
    color: `#121212`,
    fontSize: `16px`,
    lineHeight: `1.5`,
    margin: `10px 0 0 0`,
  },
})

const Education = () => {
  const classes = educationStyles()
  return (
    <Paper className={classes.root} elevation={3}>
      <div className={classes.title}>
        <span role={"img"} aria-label={"Cap emoji"}>
          🎓
        </span>{" "}
        Education
      </div>
      <div style={{ paddingLeft: "10px" }}>
        <div className={classes.school}>Louisiana State University</div>
        <div className={classes.details}>
          - Computer Science, Bachelor of Science <br />- Information Technology
          Management (ITM) Minor
        </div>
      </div>
    </Paper>
  )
}

export default Education
