import React from "react"
import Paper from "@material-ui/core/Paper"
import Divider from "@material-ui/core/Divider"
import { makeStyles } from "@material-ui/core/styles"
import "../site.css"

const experienceStyles = makeStyles({
  root: {
    display: `flex`,
    flexDirection: `column`,
    maxWidth: `700px`,
    padding: `10px`,
  },
  title: {
    color: `#121212`,
    fontSize: `1.6em`,
    fontWeight: 800,
    marginBottom: `10px`,
  },
  company: {
    color: `#121212`,
    fontSize: `1em`,
    lineHeight: `1.3`,
  },
  onSite: {
    color: `#05386b`,
    fontSize: `1em`,
    opacity: `.9`,
  },
  remote: {
    color: `#DF5015`,
    fontSize: `1em`,
  },
  position: {
    color: `#121212`,
    fontSize: `1.1em`,
    fontWeight: `600`,
  },
  details: {
    color: `#121212`,
    fontSize: `1em`,
    margin: `10px 0 0 0`,
    lineHeight: `1.5`,
  },
})

const Experience = () => {
  const classes = experienceStyles()
  return (
    <Paper className={classes.root} elevation={3}>
      <div className={classes.title}>
        <span role={"img"} aria-label={"Computer emoji"}>
          🖥️
        </span>{" "}
        Experience
      </div>
      <div style={{ paddingLeft: "10px" }}>
        <div className={classes.position}>
          Software Developer | <span className={classes.remote}>Remote</span>
        </div>
        <div className={classes.company}>
          Applied Research Associates, Inc. <br />
          June 2017 - Present
        </div>
        <div className={classes.details}>
          - Primarily develop progressive web apps and Android apps
          <br />
          - Developed native mobile applications with Android Studio and Xamarin
          Forms
          <br />
          - Considerable experience with Angular, JavaScript/Typescript,
          HTML5/CSS
          <br />
          - Experience with backend tools such as SQL Server, Identity Server,
          LLBLGen
          <br />- Scrum methods, including daily stand-ups, product backlogs,
          effort estimation
        </div>
        <Divider style={{ background: `#121212`, margin: `10px 0` }} />
        <div className={classes.position}>
          Website &amp; Front-End Developer |{" "}
          <span className={classes.onSite}>On-Site</span>
        </div>
        <div className={classes.company}>
          JAB Software, LLC. <br />
          May 2014 - May 2016
        </div>
        <div className={classes.details}>
          - Developed company{" "}
          <a
            href={`https://jabcode.com/`}
            rel={"noopener noreferrer"}
            target={`_blank`}
            className={"jab-site"}
          >
            website
          </a>{" "}
          using WordPress, HTML5, SASS <br />
          - Developed SPAs with teammates using Angular, Material Design <br />
          - Tested server-side and client-side software for web applications
          <br />- Created bug reports and user stories using Microsoft’s TFS and
          PivotalTracker
        </div>
      </div>
    </Paper>
  )
}

export default Experience
