import React from "react"
import { Link } from "gatsby"
import Button from "@material-ui/core/Button"
import Paper from "@material-ui/core/Paper"
import CodeIcon from "@material-ui/icons/Code"
import InfoIcon from "@material-ui/icons/Info"
import { ThemeProvider, makeStyles } from "@material-ui/core/styles"
import "../site.css"

const hpcStyles = makeStyles({
  title: {
    color: `#121212`,
    fontSize: `1.6em`,
    fontWeight: 800,
    marginBottom: `5px`,
  },
  subtitle: {
    color: `#121212`,
    fontSize: `1.2em`,
    fontWeight: 600,
    marginBottom: `10px`,
  },
  details: {
    color: `#121212`,
    fontSize: `1em`,
    lineHeight: `1.5`,
  },
  buttons: { display: "flex", justifyContent: "space-evenly", padding: "10px" },
})

const HPC = ({ theme }) => {
  const classes = hpcStyles()
  return (
    <Paper className={`hpc-card-root`} elevation={3}>
      <div className={classes.title}>HPC Budgeting</div>
      <div className={classes.subtitle}>
        Web application for Healing Place Church
      </div>
      <div style={{ paddingLeft: "10px" }}>
        <div className={classes.details}>
          Angular web app built with Material UI and Kendo UI components
        </div>
        <div className={classes.buttons}>
          <ThemeProvider theme={theme}>
            <Link
              to={`/hpcinfo`}
              style={{ color: "inherit", textDecoration: "none" }}
            >
              <Button
                color="primary"
                variant="contained"
                startIcon={<InfoIcon size="medium" />}
              >
                ABOUT
              </Button>
            </Link>
            <Button
              color="secondary"
              variant="contained"
              startIcon={<CodeIcon size="medium" />}
            >
              PRIVATE REPO
            </Button>
          </ThemeProvider>
        </div>
      </div>
    </Paper>
  )
}

export default HPC
